<template>
  <header class="nav">
    <div class="nav-top">
      <router-link to="/" title="Lowco.tv"><img class='logo' src="../assets/graphics/logo.png" alt="lowco-logo"/></router-link>
      <button class="skipToMain" v-on:click="skipToMain">Skip to Main Content</button>
      <social-media-icons class="social" :iconSize='1.5'></social-media-icons>
    </div>
    <div class="nav-mid">
      <div v-parallax.absY="-0.2">
        <transition v-bind:name="slideLeft ? 'slide-fade-left' : 'slide-fade-right'" mode="out-in">
          <div class="image-wrap" :key="routeName">
            <picture >
              <source :srcset="'/static/header/' + routeName + 'Header.jpg'" media="(min-width: 3000px)">
              <source :srcset="'/static/header/' + routeName + 'Header-3000px.jpg'" media="(min-width: 2000px)">
              <source :srcset="'/static/header/' + routeName + 'Header-2000px.jpg'" media="(min-width: 1000px)">
              <source :srcset="'/static/header/' + routeName + 'Header-1000px.jpg'" media="(min-width: 500px)">
              <source :srcset="'/static/header/' + routeName + 'Header-500px.jpg'" media="(min-width: 0px)">
              <img :src="'/static/header/' + routeName + 'Header-1000px.jpg'" alt="Lowco streaming from the warframe booth at Twitchcon 2017">
            </picture>
          </div>
        </transition>
      </div>
      <div class="centered">
        <div v-on:click="mole" @keyup.enter="mole" tabindex="0" aria-label="Start Easter-Egg" role="button" class="logo-sm">
          <img :class="{loaded: isLoaded}" src="../assets/graphics/logo-70x70.png" alt="lowco logo"/>
          <img class="gold" :class="{loaded: isLoaded}" src="../assets/graphics/logo-gold-70x70.png" alt="lowco gold logo"/>
        </div>
        <h1>Content Creator - Variety Gamer</h1>
        <a class="link-btn" href="https://www.twitch.tv/lowco" aria-label="External link: Visit lowco's live stream on twitch" title="twitch.tv/lowco">Live Stream</a>
      </div>
    </div>
    <nav class="nav-bar"><!--
      --><router-link title="home" to="/" exact><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>home</div></router-link><!--
      
      --><router-link title="calendar" to="/calendar/" @focus.native="collapse('community')"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>calendar</div></router-link><!--
      
      --><router-link title="community" to="/community/" @mouseover.native="expand('community')" @mouseleave.native="collapse('community')" @focus.native="expand('community')" :class="{highlighted: expandCommunity}"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>community</div></router-link><!--

      --><sub-nav :target="(expandCommunity || routeName === 'Community' || routeName === 'Equipment') && !expandPortfolio ? 'community' : '' " @mouseover.native="hovering(previousHoverTarget)" @mouseleave.native="notHovering('community')" @focus.native="expand('community')" blur.native="collapse('community')"></sub-nav><!--
      
      --><router-link title="portfolio" to="/portfolio/" @mouseover.native="expand('portfolio')" @mouseleave.native="collapse('portfolio')" @focus.native="expand('portfolio')" :class="{highlighted: expandPortfolio}"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>portfolio</div></router-link><!--
      
      --><sub-nav :target="(expandPortfolio || routeName === 'Portfolio') && !expandCommunity ? 'portfolio' : ''" @mouseover.native="hovering(previousHoverTarget)" @mouseleave.native="notHovering('portfolio')" @focus.native="expand('portfolio')" @blur.native="collapse('portfolio')"></sub-nav><!--
      
      --><router-link title="contact" aria-label="contact me" to="/contact/" @focus.native="collapse('portfolio')"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>contact</div></router-link><!--
      
      --><a href="https://www.twitch.tv/lowco/" aria-label="external link: twitch stream" title="twitch.tv/lowco" class="stream"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>stream<icon name="external-link" class="external"></icon></div></a><!--

      --><a href="https://www.designbyhumans.com/shop/Lowco/" aria-label="external link: merch store" title="designbyhumans.com/shop/Lowco/" class="store"><div class="nav-link-wrap"><icon name="chevron-down" class="icon"></icon>merch<icon name="external-link" class="external"></icon></div></a><!--
    --></nav>
  </header>
</template>

<script>
import SocialMediaIcons from './header/SocialMediaIcons'
import SubNav from './header/SubNav'

export default {
  name: 'header-bar',
  components: {SocialMediaIcons, SubNav},
  props: ['slideLeft'],
  data () {
    return {
      isLoaded: false,
      expandCommunity: false,
      expandPortfolio: false,
      hoveringSubNav: false,
      previousHoverTarget: ''
    }
  },
  computed: {
    routeName: function () {
      // equipment page has the community header
      switch (this.$route.name) {
        case '404':
          return 'Home'
        case 'Equipment':
        case 'Community-Blog':
          return 'Community'
        case 'Portfolio-Blog':
          return 'portfolio'
        default:
          return this.$route.name ? this.$route.name : 'Home'
      }
    }
  },
  methods: {
    skipToMain: function () {
      var main = document.getElementById('main')
      main.focus()
    },
    hovering: function (previousTarget) {
      this.hoveringSubNav = true
      if (previousTarget === 'community') {
        this.expandCommunity = true
      }
      if (previousTarget === 'portfolio') {
        this.expandPortfolio = true
      }
    },
    notHovering: function (target) {
      this.hoveringSubNav = false
      this.collapse(this.previousHoverTarget)
      this.previousHoverTarget = target
    },
    mole: function () {
      if (typeof window.innerWidth !== 'undefined' && typeof window.innerHeight !== 'undefined' && window.innerWidth > 850 && window.innerHeight > 650) {
        this.$emit('game-start')
      } else {
        console.log('minimum width/height: 850px / 650px')
      }
    },
    expand: function (target) {
      if (target === 'community') {
        this.expandCommunity = true
        this.expandPortfolio = false
      }
      if (target === 'portfolio') {
        this.expandCommunity = false
        this.expandPortfolio = true
      }
    },
    collapse: function (target) {
      if (target === 'community' && !this.hoveringSubNav) {
        this.expandCommunity = false
        this.previousHoverTarget = 'community'
      }
      if (target === 'portfolio' && !this.hoveringSubNav) {
        this.expandPortfolio = false
        this.previousHoverTarget = 'portfolio'
      }
    }
  },
  mounted: function () {
    this.$nextTick(() => {
      if (typeof window.innerWidth !== 'undefined' && typeof window.innerHeight !== 'undefined' && window.innerWidth > 850 && window.innerHeight > 650) {
        setTimeout(() => {
          this.isLoaded = true
        }, 90000)
      }
    })
  }
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../variables.scss';
.skipToMain {
  position: absolute;
  left: -99999px;
  top: 0;
  margin-left: 0;
  text-align: center;
  list-style: outside none;
  background: $dark-purple;
  color: $font-white;
  border: none;
  &:focus {
    position: relative;
    display: inline-block;
    background: $dark-purple;
    left: 0;
  }
}
.nav {
  text-transform: uppercase;
  h1 {
    color: $font-white;
    margin: 0 0 $standard-spacing 0;
  }
  .logo {
    width: 200px;
    transition: $standard-transition;
  }
  &-top {
    position: absolute;
    top: 5px;
    left: 0;
    right: 0;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    width: 90%;
    max-width: 1500px;
    margin: 0 auto;
    .social {
      transition: $standard-transition;
      display: inline-block;
      margin: 24px 0;
      white-space: nowrap;
    }
    a:hover {
      img {
        transform: scale(1.1);
      }
    }
  }
  &-mid {
    height: 34.5vw;
    min-height: 175px;
    background: #2D3040;
    overflow: hidden;
    position: relative;
    .image-wrap {
      img{
        min-width: $min-page-width;
        width: 100%;
      }
    }
    .centered {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      h1 {
        font-size: 2vw;
        text-shadow: 2px 2px 10px $dark-purple;
      }
      .logo-sm {
        position: relative;
        width: 5vw;
        height: 5vw;
        min-width: 70px;
        min-height: 70px;
        max-height: 100px;
        margin: $standard-spacing-half auto;
        img {
          position: absolute;
          width: 100%;
          left: 0;
          top: 0;
          border: 2px solid $light-purple;
          border-radius: 70px;
          box-shadow: 0 0 0 $light-purple;
          opacity: 1;
          cursor: pointer;
          &.gold {
            border: 2px solid $light-purple;
            opacity: 0;
            transition: all 30000ms ease-in, box-shadow 250ms ease-in-out, transform 750ms ease-in-out, visibility 0ms ease-in-out;
            &:hover, &:focus {
              box-shadow: 0 0 25px $light-purple;
              transform: scale(1.05);
            }
            &.loaded {
              opacity: 1;
            }
          }
          &.loaded {
            opacity: 0;
          }
        }
      }
    }
  }
  &-bar {
    background-color: $dark-purple;
    white-space: nowrap;
    .icon {
      display: block;
      visibility: hidden;
      margin: 0 auto;
      fill: $font-white;
    }
    .external {
      margin: 0 5px;
      fill: $font-white;
      vertical-align: center;
      width: 0.75em;
      height: 0.75em;
    }
    a {
      display: inline-flex;
      position: relative;
      flex-direction: column;
      padding: $standard-spacing3 $standard-spacing-half/2 $standard-spacing3 $standard-spacing-half/2+2;
      color: $font-white;
      min-width: 100px;
      transition: $standard-transition;
      transition-delay: 25ms;
      margin: 0 1px 0 0;
      .nav-link-wrap {
        padding-bottom: 20px;
      }
      &~a {
        .nav-link-wrap:before {
          content:"";
          position: absolute;
          left: -1px;
          background: #1e1e1e;
          height: $standard-spacing4;
          width: 1px;
        }
      }
      &:hover,
      &:focus {
        background-color: $light-purple;
      }
    }
    .router-link-active {
      background-color: $light-purple;
      a {
        color: $font-white;
      }
      .icon {
        visibility: visible;
      }
    }
    .highlighted { 
      background-color: $light-purple;
    }
  }
  &.small {
    background-color: $dark-purple;
    .nav-top {
      position: inherit;
    }
  }
}

.return{
  border: 2px solid $light-purple;
  background: none;
  border-radius: 25px;
  transition: $standard-transition;
  font-family: inherit;
  letter-spacing: inherit;
  color: $font-white;
  padding: ($standard-spacing/3)*2 $standard-spacing;
  &:hover, &:focus, &.selected{
    cursor: pointer;
    background-color: $light-purple;
  }
  &-wrap{
    display: flex;
    max-width: 57em;
    margin: -$standard-spacing auto $standard-spacing auto;
    justify-content: space-between;
    color: $font-white;
  }
  svg{
    vertical-align: text-bottom;
  }
}
@media (max-width: 780px) {
  .nav-top {
    text-align: center;
    width: 97%;
    .social{
      margin-top: 12px;
    }
    .logo{
      width: 150px;
    }
  }
  .nav-bar a {
    min-width: 0;
    padding: $standard-spacing 1em;
  }
  .nav-link-wrap{
    font-size: smaller;
    font-weight: lighter;
  }
  .nav-mid {
    .centered {
      width: 100%;
      margin-top: -10px;
      .logo-sm {
        visibility: hidden;
      }
    }
  }
}
@media (max-width: 640px) {
  .nav-bar a.store {
    display: none;
  }
}
@media (max-width: 555px) {
  .nav-bar a {
    padding: $standard-spacing 0.45em;
  }
}
</style>



