import Vue from 'vue'
import Router from 'vue-router'
const Home = () => import('@/components/Home.vue')
const Calendar = () => import('@/components/Calendar.vue')
const Portfolio = () => import('@/components/Portfolio.vue')
const Community = () => import('@/components/Community.vue')
const Blog = () => import('@/components/portfolio/Blog.vue')
const BlogList = () => import('@/components/portfolio/BlogList.vue')
const Contact = () => import('@/components/Contact.vue')
const Redirect = () => import('@/components/Redirect.vue')
const Equipment = () => import('@/components/Equipment.vue')
const Mixer = () => import('@/components/Mixer.vue')
const ErrorPage = () => import('@/components/_404.vue')

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/calendar',
      name: 'Calendar',
      component: Calendar
    },
    {
      path: '/community/:filterTag?',
      component: Community,
      children: [
        {
          path: 'equipment',
          name: 'Equipment',
          component: Equipment
        },
        {
          path: '',
          name: 'Community',
          component: BlogList
        },
        {
          path: 'blog/:year/:month/:blogId',
          name: 'Community-Blog',
          component: Blog
        }]
    },
    {
      path: '/portfolio/:filterTag?',
      component: Portfolio,
      children: [
        {
          path: '',
          name: 'Portfolio',
          component: BlogList
        },
        {
          path: 'blog/:year/:month/:blogId',
          name: 'Portfolio-Blog',
          component: Blog
        }]
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
    {
      path: '/mixer',
      name: 'Mixer',
      component: Mixer
    },
    {
      path: '/redirect/*',
      name: 'Redirect',
      component: Redirect
    },
    {
      path: '/404',
      name: '404',
      component: ErrorPage
    },
    {
      path: '*',
      redirect: to => {
        // Handle dynamic redirects
        /* eslint-disable */
        var redirects = _redirects !== undefined ? _redirects : null
        if (redirects !== null) {
          for (var i = 0; i < redirects.length; i++) {
            if (to.path.toLowerCase() === redirects[i].path.toLowerCase() || to.path.toLowerCase() === redirects[i].path.toLowerCase() + '/') {
              window.location.replace(redirects[i].redirect)
              return 'redirect' + to.path.toLowerCase()
            }
          }
        }
        Vue.$ga.page('/404' + to.path.toLowerCase())
        return '/404'
        /* eslint-enable */
      }
    }
  ]
})



