<template>
  <nav class="nav-bar sub-nav" @mouseover.native="target = target" @mouseleave.native="target = target" :class="{expanded: target !== ''}">
    <transition name="delay">
      <section v-if="target != ''">
        <router-link v-for="link in targetLinkList" :title="link.label.charAt(0).toUpperCase() + link.label.slice(1)" :aria-label="target + ' sub page ' + link.label" :key="link.$index" :to="link.to"><div class="wrapper">{{link.label}}</div></router-link>
      </section>
    </transition>
  </nav>
</template>

<script>
export default {
  name: 'sub-nav',
  props: ['target'],
  computed: {
    targetLinkList: function () {
      if (this.target === 'community') {
        this.previousLinks = this.communityLinks.slice()
        return this.communityLinks
      }
      if (this.target === 'portfolio') {
        this.previousLinks = this.portfolioLinks.slice()
        return this.portfolioLinks
      }
      return this.previousLinks
    }
  },
  data () {
    return {
      previousLinks: [],
      communityLinks: [{
        to: '/community/giveaways/',
        label: 'giveaways'
      }, {
        to: '/community/faq/',
        label: 'FAQ'
      }, {
        to: '/community/events/',
        label: 'events'
      }, {
        to: '/community/equipment/',
        label: 'equipment'
      }],
      portfolioLinks: [{
        to: '/portfolio/charity/',
        label: 'charity'
      }, {
        to: '/portfolio/industry/',
        label: 'industry'
      }, {
        to: '/portfolio/promotions/',
        label: 'promotions'
      }]
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.delay-enter-active, .delay-leave-active {
  transition-delay: 10ms;
}

.sub-nav {
  position: absolute;
  width: 100%;
  min-width: $min-page-width;
  overflow: hidden;
  text-transform: uppercase;
  background-color: $light-purple;
  margin-top: 0;
  transition: height .2s ease-in;
  height: 0;
  &.expanded, &.expanded:hover {
    transition: height .3s ease-in;
    height: 4.25em;
    z-index: 1;
  }
  a {
    display: inline-flex;
    flex-direction: column;
    position: relative;
    padding: $standard-spacing+9 $standard-spacing $standard-spacing+5 $standard-spacing;
    min-width: 100px;
    min-width: 0;
    color: $font-white;
    margin-left: -2px;
    vertical-align: top;
    &~a:before {
      content:"";
      position: absolute;
      left: -1px;
      background: #1e1e1e;
      height: 33%;
      width: 1px;
    }
    .wrapper {
      padding-bottom: 1px;
      padding-top: 1px;
      transition: $standard-transition;
    }
    &:hover,
    &:focus,
    &.router-link-active {
      .wrapper {
        padding-bottom: 5px;
        border-bottom: 1px solid $font-white;
      }
    }
  }
}

@media (max-width: 460px) {
  .sub-nav a {
    padding: $standard-spacing+9 $standard-spacing-half $standard-spacing+5 $standard-spacing-half;
  }
}
</style>



