<template>
  <footer class="footer">
    <div class="wrap">
      <span>LOWCO.TV &copy; 2017</span>
      <social-media-icons class="social" :iconSize='1.5'></social-media-icons>
    </div>
  </footer>
</template>

<script>
import SocialMediaIcons from './header/SocialMediaIcons'

export default {
  name: 'footer',
  components: {SocialMediaIcons}
}
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
.footer{
  background-color: $dark-purple;
  color: $font-white;
  padding: $standard-spacing $standard-spacing2;
  text-align: left;
  .wrap{
    width: 90%;
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
  }
  span{
    display: inline-block;
    vertical-align: middle;
  }
  .social{
    display: inline-block;
    float: right;
    margin-right: -$standard-spacing;
  }
}
@media (max-width: 650px) {
  .footer {
    padding: $standard-spacing;
    .social {
      margin-top: -4px;
      .icon {
        margin: 0 6px;
      }
    }
  }
}
</style>



