<template>
  <main id="container">
    <div class="game-wrapper" v-on:click="stopGame"></div><div id="game-container" tabindex="0"></div>
  </main>
</template>

<script>
  import Game from './js/game-controller.js'

  export default {
    name: 'game',
    data () {
      return {
        showGame: false,
        cursor: 0,
        winningCombo: [50, 53, 50, 53]
      }
    },
    methods: {
      runGame () {
        setTimeout(() => {
          if (this.$ga !== undefined) {
            this.$ga.event({
              eventCategory: 'Game',
              eventAction: 'runGame',
              eventLabel: 'Game Start'
            })
          }
          Game.Run()
        }, 500)
        this.showGame = true
      },
      stopGame () {
        this.showGame = false
        var event = new Event('closeGame')
        dispatchEvent(event)
        this.$emit('game-end')
      }
    },
    created () {
      this.runGame()
      const self = this
      window.addEventListener('keyup', function (e) {
        if (e.keyCode === 27) {
          self.stopGame()
        }
      })
    }
}
</script>
<style lang="scss">
.game-wrapper{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  margin-left: auto;
  margin-right: auto;
  background-color: rgb(0, 0, 0);
}
</style>



