/* eslint-disable no-undef */
global.p2 = require('p2')
global.PIXI = require('pixi.js')
global.Phaser = require('phaser-ce')

export default {
  Run () {
    var game = new Phaser.Game(800, 600, Phaser.CANVAS, 'game-container', {preload: preload, create: create, update: update, render: render})
    var player
    var boss
    var greenEnemies
    var blueEnemies
    var blueEnemyBullets
    var starfield
    var cursors
    var bank
    var explosions
    var playerDeath
    var bossDeath
    var bullets
    var hearts
    var fireButton
    var pickup
    var pickupSound
    var dropTimer = 0
    var dropRate = 3000
    var bulletTimer = 0
    var greenEnemyLaunchTimer
    var greenEnemySpacing = 1000
    var blueEnemyLaunchTimer
    var blueEnemyLaunched = false
    var blueEnemySpacing = 2500
    var bossLaunchTimer
    var bossLaunched = false
    var bossAttackTimer = 0
    var bossAttackRate = 4000
    var gameRunning = false
    var potatoSound
    var lowcoRip
    var soundtrack
    var deathRay
    var deathRayCollider
    var moleyAttack
    var intro
    var gameOver
    var gameStart
    var gameWin
    var gotemLeft
    var gotemRight
    var mute
    var score = 0

    var ACCLERATION = 600
    var DRAG = 400
    var MAXSPEED = 400
    var ENEMY_SPEED = 450

    // sounds
    var bewm
    var bewmLong
    var oww
    var owwLong
    var bossLaser
    var bossMoley
    var bossBlob
    var winSound
    var lasers = []

    function preload () {
      game.load.image('starfield', '/static/game/sprites/starfield.jpg')
      game.load.image('ship', '/static/game/sprites/player.png')
      game.load.image('ship-fire', '/static/game/sprites/player-fire.png')
      game.load.image('bullet', '/static/game/sprites/bullet.png')
      game.load.image('heart', '/static/game/sprites/love.png')
      game.load.spritesheet('moleys', '/static/game/sprites/moleys.png', 240, 144)
      game.load.image('booker-moley', '/static/game/sprites/booker-moley.png')
      game.load.image('holy-moley', '/static/game/sprites/holy-moley.png')
      game.load.image('jester-moley', '/static/game/sprites/jester-moley.png')
      game.load.image('lips-moley', '/static/game/sprites/lips-moley.png')
      game.load.image('small-moley', '/static/game/sprites/small-moley.png')
      game.load.image('sod-moley', '/static/game/sprites/sod-moley.png')
      game.load.image('star-moley', '/static/game/sprites/star-moley.png')
      game.load.image('lowcomoley', '/static/game/sprites/lowcomoley.png')
      game.load.image('lowcoangrymoley', '/static/game/sprites/lowcoangrymoley.png')
      game.load.image('enemy-blue', '/static/game/sprites/enemy-blue.png')
      game.load.image('blueEnemyBullet', '/static/game/sprites/enemy-blue-bullet.png')
      game.load.image('title-screen', '/static/game/sprites/title-screen.png')
      game.load.image('game-over', '/static/game/sprites/gameover.png')
      game.load.image('game-win', '/static/game/sprites/winScreen.png')
      game.load.image('deathRay', '/static/game/sprites/death-ray.png')
      game.load.image('lowco-rip', '/static/game/sprites/rip.png')
      game.load.image('gotem', '/static/game/sprites/lowcoGotEm.png')
      game.load.image('gotemRight', '/static/game/sprites/lowcoGotEmRight.png')
      game.load.image('potato', '/static/game/sprites/aim.png')
      game.load.image('hype', '/static/game/sprites/hype.png')
      game.load.image('mute', '/static/game/sprites/lowcoSing.png')
      game.load.image('mute-gray', '/static/game/sprites/lowcoSing-muted.png')
      game.load.image('heart', '/static/game/sprites/love.png')
      game.load.spritesheet('explosion', '/static/game/sprites/explode.png', 128, 128)
      game.load.audio('bewm', '/static/game/audio/bewm-short.mp3')
      game.load.audio('oww', '/static/game/audio/oww-short.mp3')
      game.load.audio('intro', '/static/game/audio/intro.mp3')
      game.load.audio('bewm-long', '/static/game/audio/bewm.mp3')
      game.load.audio('oww-long', '/static/game/audio/oww-2.mp3')
      game.load.audio('all-right', '/static/game/audio/allright.mp3')
      game.load.audio('potato', '/static/game/audio/potato.mp3')
      game.load.audio('boss-laser', '/static/game/audio/laser.mp3')
      game.load.audio('boss-death', '/static/game/audio/bossDeath.mp3')
      game.load.audio('boss-moley', '/static/game/audio/moleyRaid.mp3')
      game.load.audio('boss-blob', '/static/game/audio/boss-attack-3.mp3')
      game.load.audio('soundtrack', '/static/game/audio/Axtasia.mp3')
      game.load.audio('laser1', '/static/game/audio/laser1.mp3')
      game.load.audio('laser2', '/static/game/audio/laser2.mp3')
      game.load.audio('laser3', '/static/game/audio/laser3.mp3')

      // set up close listener
      window.addEventListener('closeGame', function (e) {
        game.destroy()
      })
    }

    function create () {
      // The scrolling starfield background
      starfield = game.add.tileSprite(0, 0, 800, 600, 'starfield')

      // Sounds
      bewm = game.add.audio('bewm', 0.25)
      oww = game.add.audio('oww', 0.25)
      bewmLong = game.add.audio('bewm-long', 0.5)
      owwLong = game.add.audio('oww-long', 0.35)
      bossLaser = game.add.audio('boss-laser', 0.35)
      bossMoley = game.add.audio('boss-moley', 0.15)
      bossBlob = game.add.audio('boss-blob', 0.35)
      winSound = game.add.audio('boss-death', 0.35)
      pickupSound = game.add.audio('all-right', 1)
      potatoSound = game.add.audio('potato', 1)
      intro = game.add.audio('intro', 0.75)
      soundtrack = game.add.audio('soundtrack', 0.1)
      for (var i = 1; i < 4; i++) {
        lasers[i - 1] = game.add.audio('laser' + i, 0.25)
      }
      // game.sound.setDecodedCallback([ bewm ], start, this)

      // Bullet Group
      bullets = game.add.group()
      bullets.enableBody = true
      bullets.physicsBodyType = Phaser.Physics.ARCADE
      bullets.createMultiple(100, 'bullet')
      bullets.setAll('anchor.x', 1)
      bullets.setAll('anchor.y', 0.5)
      bullets.setAll('scale.x', 0.75)
      bullets.setAll('scale.y', 0.75)
      bullets.setAll('outOfBoundsKill', true)
      bullets.setAll('checkWorldBounds', true)

      // The hero
      player = game.add.sprite(100, 300, 'ship')
      player.health = 120
      player.exists = false
      player.alive = false
      player.potato = false
      player.anchor.set(0.5)
      player.scale.x = 0.35
      player.scale.y = 0.35
      player.scaleMax = 0.5
      game.physics.enable(player, Phaser.Physics.ARCADE)
      player.body.maxVelocity.setTo(MAXSPEED, MAXSPEED)
      player.body.drag.setTo(DRAG, DRAG)
      player.weaponLevel = 1
      player.lastShot = game.time.now

      // Health Group
      hearts = game.add.group()
      for (var h = 0; h < player.health / 20; h++) {
        hearts.create(675 - 130 * h, 40, 'heart')
      }
      hearts.scale.set(0.30, 0.30)

      // Remove one heart
      player.health -= 20
      var _heart = hearts.getFirstExists()
      if (_heart) { _heart.kill() }

      // Pickups
      pickup = game.add.group()
      pickup.enableBody = true
      pickup.physicsBodyType = Phaser.Physics.ARCADE
      pickup.create(0, 0, 'heart', false)
      pickup.create(0, 0, 'hype', false)
      pickup.create(0, 0, 'potato', false)
      pickup.setAll('visible', false)
      pickup.forEach(function (drop) {
        drop.scale.setTo(0.35, 0.35)
        drop.alive = false
      })

      // Gotems
      gotemLeft = game.add.sprite(0, game.height - 120, 'gotem')
      gotemRight = game.add.sprite(game.width - 110, game.height - 120, 'gotemRight')
      gotemLeft.visible = false
      gotemRight.visible = false

      // The Enemies
      greenEnemies = game.add.group()
      greenEnemies.enableBody = true
      greenEnemies.physicsBodyType = Phaser.Physics.ARCADE
      greenEnemies.createMultiple(2, 'moleys', [0, 2, 3, 1, 4])
      greenEnemies.setAll('anchor.x', 0.5)
      greenEnemies.setAll('anchor.y', 0.5)
      greenEnemies.setAll('scale.x', 0.25)
      greenEnemies.setAll('scale.y', -0.25)
      greenEnemies.setAll('angle', 0)
      greenEnemies.forEach(function (enemy) {
        addEnemyEmitterTrail(enemy)
        enemy.damageAmount = 20
        enemy.events.onKilled.add(function () {
          enemy.trail.kill()
        })
      })

      // The Boss
      boss = game.add.sprite(0, 0, 'lowcomoley')

      boss.scale.setTo(1.15, -1.15)
      boss.exists = false
      boss.alive = false
      boss.anchor.setTo(0.5, 0.5)
      boss.damageAmount = 20
      boss.angle = 90
      game.physics.enable(boss, Phaser.Physics.ARCADE)
      boss.body.maxVelocity.setTo(100, 80)
      boss.dying = false
      boss.body.setCircle(120, 70, 30)

      // Blue enemy's bullets
      blueEnemyBullets = game.add.group()
      blueEnemyBullets.enableBody = true
      blueEnemyBullets.physicsBodyType = Phaser.Physics.ARCADE
      blueEnemyBullets.createMultiple(30, 'blueEnemyBullet')
      blueEnemyBullets.callAll('crop', null, {x: 90, y: 0, width: 90, height: 70})
      blueEnemyBullets.setAll('alpha', 0.9)
      blueEnemyBullets.setAll('anchor.x', 0.5)
      blueEnemyBullets.setAll('anchor.y', 0.5)
      blueEnemyBullets.setAll('outOfBoundsKill', true)
      blueEnemyBullets.setAll('checkWorldBounds', true)
      blueEnemyBullets.forEach(function (enemy) {
        enemy.body.setSize(20, 15, 22, 25)
      })

      // blue enemies
      blueEnemies = game.add.group()
      blueEnemies.enableBody = true
      blueEnemies.physicsBodyType = Phaser.Physics.ARCADE
      blueEnemies.createMultiple(30, 'enemy-blue')
      blueEnemies.setAll('anchor.x', 0.5)
      blueEnemies.setAll('anchor.y', 0.5)
      blueEnemies.setAll('scale.x', 0.25)
      blueEnemies.setAll('scale.y', -0.25)
      blueEnemies.forEach(function (enemy) {
        enemy.damageAmount = 20
      })

      // The Boss's Death ray
      deathRay = game.add.sprite(0, 0, 'deathRay')
      deathRay.alive = false
      deathRay.visible = false
      deathRay.crop({x: 0, y: 0, width: 40, height: 40})
      deathRay.anchor.x = 0.5
      deathRay.anchor.y = 0.5
      deathRay.update = function () {
        this.alpha = game.rnd.realInRange(0.8, 1)
      }

      deathRayCollider = game.add.sprite(0, 0)
      deathRayCollider.alive = false
      deathRayCollider.visible = false
      deathRayCollider.damageAmount = boss.damageAmount
      game.physics.enable(deathRayCollider, Phaser.Physics.ARCADE)
      deathRayCollider.body.setSize(game.width, 1)
      boss.addChild(deathRay)

      // The Boss's Fish Moley Attack
      moleyAttack = game.add.group()
      moleyAttack.enableBody = true
      moleyAttack.physicsBodyType = Phaser.Physics.ARCADE
      moleyAttack.createMultiple(6, 'moleys', [0, 1, 2, 3, 4])
      moleyAttack.setAll('anchor.x', 0.5)
      moleyAttack.setAll('anchor.y', 0.5)
      moleyAttack.setAll('scale.x', 0.25)
      moleyAttack.setAll('scale.y', -0.25)
      moleyAttack.setAll('angle', 0)
      moleyAttack.forEach(function (enemy) {
        enemy.damageAmount = 20
      })

      boss.finishOff = function () {
        if (!boss.dying) {
          boss.dying = true
          bossDeath.x = boss.x
          bossDeath.y = boss.y
          bossDeath.start(false, 1000, 50, 20)

          // kill boss after explosions
          game.time.events.add(1000, function () {
            var explosion = explosions.getFirstExists(false)
            var beforeScaleX = explosions.scale.x
            var beforeScaleY = explosions.scale.y
            var beforeAlpha = explosions.alpha
            explosion.reset(boss.body.x + boss.body.halfWidth, boss.body.y + boss.body.halfHeight)
            explosion.alpha = 0.4
            explosion.scale.x = 3
            explosion.scale.y = 3
            var animation = explosion.play('explosion', 30, false, true)
            animation.onComplete.addOnce(function () {
              explosion.scale.x = beforeScaleX
              explosion.scale.y = beforeScaleY
              explosion.alpha = beforeAlpha
            })
            if (!bewmLong.isPlaying) { bewmLong.play() }
            boss.kill()
            boss.dying = false
            bossDeath.on = false
            showWinScreen()
          })
        }
      }

      boss.fire = function () {
        if (!boss.dying) {
          boss.firing = true
          boss.loadTexture('lowcoangrymoley', 0)
          // Pick a random attack to perform
          var attack = Math.floor(Math.random() * 3) + 1
          switch (attack) {
            // Laser sweep
            case 1:
              var chargeTime = 1000
              var rayTime = 1000
              deathRayCollider.reset(0, 0)
              deathRay.revive()
              deathRay.x = boss.width / 6
              deathRay.y = -boss.height / 16
              deathRay.alpha = 0
              deathRay.angle = 0
              deathRay.scale.x = 10
              deathRay.scale.y = 10
              game.add.tween(deathRay).to({alpha: 1}, chargeTime, Phaser.Easing.Linear.In, true).onComplete.add(function (deathRay) {
                if (!bossLaser.isPlaying) {
                  bossLaser.play()
                  game.camera.shake(0.02, 500)
                }
                game.add.tween(deathRay.scale).to({x: 1, y: game.height / 2}, chargeTime, Phaser.Easing.Linear.In, true)
                // Aim at player
                var angleToPlayer = game.math.radToDeg(game.physics.arcade.angleBetween(boss, player))
                var targetAngle = 100
                // Since a physics body can't turn we use a collider that
                // either comes from the top or from the bottom
                // following the same path as the laser
                deathRayCollider.y = -game.physics.arcade.distanceBetween(boss, player)
                var colliderOverShoot = 10
                if (angleToPlayer > 0) {
                  targetAngle *= -1
                  deathRayCollider.y = game.height + game.physics.arcade.distanceBetween(boss, player)
                  colliderOverShoot *= -1
                }
                game.add.tween(deathRayCollider).to({y: boss.y + colliderOverShoot}, rayTime, Phaser.Easing.Sinusoidal.InOut, true)
                game.add.tween(deathRay).to({angle: targetAngle}, rayTime, Phaser.Easing.Linear.In, true).onComplete.add(function (deathRay) {
                  deathRayCollider.reset(0, 0)
                  deathRay.kill()
                  boss.loadTexture('lowcomoley', 0)
                  boss.firing = false
                })
              })

              break
            // Spawn a bunch of little moleys
            case 2:
              if (!bossMoley.isPlaying) {
                bossMoley.play()
                game.camera.shake(0.05, 500)
              }
              for (var h = 0; h < 3; h++) {
                for (var i = 0; i < 10; i++) {
                  let enemy = moleyAttack.getFirstExists(false)
                  if (enemy) {
                    enemy.frame = game.rnd.integerInRange(0, 4)
                    enemy.reset(game.width + enemy.width * 5 * h, (game.height / 9) * (i + 1))
                    enemy.body.velocity.x = -ENEMY_SPEED

                    enemy.angle = 180
                    enemy.offset = game.rnd.integerInRange(-5, 5)
                    // Update function for each enemy ship
                    enemy.update = function () {
                      if (game.physics.arcade.distanceBetween(enemy, player) > 175 && enemy.x > 175) {
                        var angle = game.physics.arcade.moveToXY(enemy, player.x, player.y + enemy.offset, ENEMY_SPEED)
                        enemy.angle = game.math.radToDeg(angle)
                      }

                      if (enemy.x + enemy.width / 2 < 0) {
                        enemy.kill()
                        enemy.x = game.width + enemy.width * 2
                      }
                    }
                  }
                }
              }
              game.time.events.add(4500, function () {
                boss.loadTexture('lowcomoley', 0)
              })
              game.time.events.add(6000, function () {
                boss.firing = false
                boss.loadTexture('lowcomoley', 0)
              })
              break
            // Lasers all the way down
            case 3:
              if (!bossBlob.isPlaying) {
                bossBlob.play()
                game.camera.shake(0.05, 500)
              }
              game.time.events.add(250, function () {
                launchBlueEnemy()
                game.time.events.add(2500, function () {
                  launchBlueEnemy()
                  game.time.events.add(2500, function () {
                    launchBlueEnemy()
                    boss.loadTexture('lowcomoley', 0)
                    game.time.events.add(2500, function () {
                      launchBlueEnemy()
                      game.time.events.add(4500, function () {
                        boss.firing = false
                      })
                    })
                  })
                })
              })
              break
          }
          bossAttackTimer = game.time.now + bossAttackRate
        }
      }

      boss.update = function () {
        if (!boss.alive) { return }

        deathRay.update()

        if (boss.x > game.width - boss.width / 15) {
          boss.body.acceleration.x = -50
        }
        if (boss.x < game.width - boss.width / 15) {
          boss.body.acceleration.x = 50
        }
        if (boss.y > player.y) {
          boss.body.acceleration.y = -50
        } else if (boss.y < player.y) {
          boss.body.acceleration.y = 50
        } else {
          boss.body.acceleration.y = 0
        }

        // Squish and rotate boss for banking
        var bank = boss.body.velocity.x / MAXSPEED
        boss.scale.x = 1.15 - Math.abs(bank) / 3
        boss.angle = 180 - bank * 10

        // Fire on interval
        if (game.time.now > bossAttackTimer && !boss.firing) {
          boss.fire()
        }
      }

      // Game controls
      cursors = game.input.keyboard.createCursorKeys()
      fireButton = game.input.keyboard.addKey(Phaser.Keyboard.SPACEBAR)

      // The explosion pool
      explosions = game.add.group()
      explosions.enableBody = true
      explosions.physicsBodyType = Phaser.Physics.ARCADE
      explosions.createMultiple(30, 'explosion')
      explosions.setAll('anchor.x', 0.5)
      explosions.setAll('anchor.y', 0.5)
      explosions.forEach(function (explosion) {
        explosion.animations.add('explosion')
      })

      // Big Explosion
      playerDeath = game.add.emitter(player.x, player.y)
      playerDeath.width = 50
      playerDeath.height = 50
      playerDeath.makeParticles('explosion', [0, 1, 2, 3, 4, 5, 6, 7], 10)
      playerDeath.setAlpha(0.9, 0, 800)
      playerDeath.setScale(0.1, 0.6, 0.1, 0.6, 1000, Phaser.Easing.Quintic.Out)

      // Big Explosion for boss
      bossDeath = game.add.emitter(boss.x, boss.y)
      bossDeath.width = boss.width / 2
      bossDeath.height = boss.height / 2
      bossDeath.makeParticles('explosion', [0, 1, 2, 3, 4, 5, 6, 7], 20)
      bossDeath.setAlpha(0.9, 0, 900)
      bossDeath.setScale(0.3, 1.0, 0.3, 1.0, 1000, Phaser.Easing.Quintic.Out)

      // Game Over
      lowcoRip = game.add.sprite(game.world.centerX, game.world.centerY - 50, 'lowco-rip')
      lowcoRip.anchor.setTo(0.5, 0.5)
      lowcoRip.alpha = 0
      lowcoRip.visible = false

      // Mute
      mute = game.add.sprite(game.width - 50, 50, 'mute')
      mute.scale.x = 0.5
      mute.scale.y = 0.5
      mute.anchor.setTo(0.5, 0.5)
      mute.alpha = 1
      mute.visible = true
      mute.inputEnabled = true
      mute.events.onInputDown.add(function () {
        if (game.sound.mute) {
          mute.loadTexture('mute', 0)
        } else {
          mute.loadTexture('mute-gray', 0)
        }
        game.sound.mute = !game.sound.mute
      }, this)

      // Game Over
      gameOver = game.add.sprite(game.world.centerX, game.world.centerY + 50, 'game-over')

      gameOver.x = gameOver.x - gameOver.width / 2
      gameOver.y = gameOver.y - gameOver.height / 2
      gameOver.visible = false

      // Game Win
      gameWin = game.add.sprite(game.world.centerX, game.world.centerY + 25, 'game-win')

      gameWin.inputEnabled = true
      gameWin.x = gameWin.x - gameWin.width / 2
      gameWin.y = gameWin.y - gameWin.height * 2 / 3
      gameWin.visible = false

      gameWin.events.onInputDown.add(restart, this)

      // Game Start
      gameStart = game.add.sprite(game.world.centerX, game.world.centerY, 'title-screen')

      gameStart.inputEnabled = true
      gameStart.x = gameStart.x - gameStart.width / 2
      gameStart.y = gameStart.y - gameStart.height / 2
      gameStart.visible = true

      gameStart.events.onInputDown.add(startGame, this)
      soundtrack.play()
      intro.play()
    }

    function startGame () {
      game.time.events.add(1000, launchGreenEnemy)
      player.exists = true
      player.alive = true
      gameStart.visible = false
      gameStart.exists = false
      gameRunning = true
    }

    function showWinScreen () {
      player.visible = false
      player.kill()
      gameOver.visible = false
      lowcoRip.visible = false
      gameWin.visible = true
      gotemLeft.visible = true
      gotemRight.visible = true
      gotemLeft.alpha = 0
      gotemRight.alpha = 0
      gameWin.alpha = 0
      var fadeInGottemLeft = game.add.tween(gotemLeft)
      fadeInGottemLeft.to({alpha: 1}, 3000, Phaser.Easing.Quintic.Out)
      fadeInGottemLeft.start()
      var fadeInGottemRight = game.add.tween(gotemRight)
      fadeInGottemRight.to({alpha: 1}, 3000, Phaser.Easing.Quintic.Out)
      fadeInGottemRight.start()
      var fadeInGameWin = game.add.tween(gameWin)
      fadeInGameWin.to({alpha: 1}, 3000, Phaser.Easing.Quintic.Out)
      fadeInGameWin.onComplete.add(setResetHandlers)
      fadeInGameWin.start()
      if (!winSound.isPlaying) { winSound.play() }
    }

    function update () {
      if (gameRunning) {
        if (!soundtrack.isPlaying) { soundtrack.play() }
        // Scroll the background
        starfield.tilePosition.x -= 3

        // Reset the player, then check for movement input
        player.body.acceleration.y = 0

        // player movement
        if (cursors.up.isDown && player.y > 50) {
          player.body.acceleration.y = -ACCLERATION
          if (player.body.velocity.y > -100) {
            player.body.acceleration.y = player.body.acceleration.y * 10
          }
        } else if (cursors.down.isDown && (player.y < game.height - 50)) {
          player.body.acceleration.y = ACCLERATION
          if (player.body.velocity.y < 100) {
            player.body.acceleration.y = player.body.acceleration.y * 10
          }
        }

        // Move ship via mouse
        if (game.input.x < game.width - 20 &&
            game.input.x > 20 &&
            game.input.y > 20 &&
            game.input.y < game.height - 20 && !(cursors.up.isDown || cursors.down.isDown || fireButton.isDown)) {
          var minDist = 100
          var dist = game.input.y - player.y
          player.body.velocity.y = MAXSPEED * game.math.clamp(dist / minDist, -1, 1)
        }

        // Stop at screen edges
        if (player.y > game.height - 50) {
          player.y = game.height - 50
          player.body.acceleration.y = 0
          player.body.velocity.y = 0
        }
        if (player.y < 50) {
          player.y = 50
          player.body.acceleration.y = 0
          player.body.velocity.y = 0
        }

        // Fire Button
        if (player.alive && (fireButton.isDown || game.input.activePointer.isDown)) {
          fireBullet()
        }
        if (player.lastShot < game.time.now - 100) {
          player.loadTexture('ship', 0)
        } else {
          player.loadTexture('ship-fire', 0)
        }

        // Squish and rotate ship for illusion of "banking"
        bank = player.body.velocity.y / (MAXSPEED * 2)
        player.scale.y = 0.35 - Math.abs(bank) / 4
        player.angle = bank * 30

        // Check collisions
        // Green Enemies
        game.physics.arcade.overlap(player, greenEnemies, shipCollide, null, this)
        game.physics.arcade.overlap(greenEnemies, bullets, hitEnemy, null, this)

        // Blue enemies
        game.physics.arcade.overlap(player, blueEnemies, shipCollide, null, this)
        game.physics.arcade.overlap(blueEnemies, bullets, hitEnemy, null, this)
        game.physics.arcade.overlap(blueEnemyBullets, player, enemyShootsPlayer, null, this)

        // Boss
        game.physics.arcade.overlap(boss, bullets, hitEnemy, null, this)
        game.physics.arcade.overlap(player, moleyAttack, shipCollide, null, this)
        game.physics.arcade.overlap(moleyAttack, bullets, hitEnemy, null, this)

        // Weapon Upgrades
        game.physics.arcade.overlap(player, pickup, processPickup, null, this)

        // Since we cant rotate an arcade physics body we use a collider that traces the angular velocity of the tween or whatever
        game.physics.arcade.overlap(player, deathRayCollider, enemyShootsPlayer, null, this)

        // Gameover Check
        if (!player.alive && gameOver.visible === false && gameWin.visible === false) {
          gameOver.visible = true
          gameOver.alpha = 0
          lowcoRip.visible = true
          lowcoRip.alpha = 0

          var fadeInGameOver = game.add.tween(gameOver)
          fadeInGameOver.to({alpha: 1}, 1000, Phaser.Easing.Quintic.Out)
          fadeInGameOver.onComplete.add(setResetHandlers)
          fadeInGameOver.start()
          game.add.tween(lowcoRip).to({ alpha: 1 }, 2000, Phaser.Easing.Linear.None, true, 0, 0, false)
        }
      }
    }

    function render () {
      /* game.debug.body(deathRay);
      game.debug.body(player);
      game.debug.body(deathRayCollider)
      game.debug.body(boss)
      for (var i = 0; i < pickup.length; i++)
      {
        game.debug.body(pickup.children[i]);
      } */
    }

    function setResetHandlers () {
      // The click to restart handler
      var tapRestart = game.input.onTap.addOnce(_restart, this)
      var spaceRestart = fireButton.onDown.addOnce(_restart, this)
      function _restart () {
        tapRestart.detach()
        spaceRestart.detach()
        restart()
      }
    }

    function updateGameState () {
      if (gameRunning) {
        if (!bossLaunched) {
          // Pacing (Enemies come quicker as score increases)
          greenEnemySpacing *= 0.9

          // Blue enemies come in after a score of 1000
          if (!blueEnemyLaunched && score >= 1000) {
            blueEnemyLaunched = true
            launchBlueEnemy()
            // Slow green enemy spawn rate now that there are more enemies
            greenEnemySpacing *= 2
          }
        }

        // LaunchBoss after a certain point
        if (!bossLaunched && score > 52500) {
          // Stop spawning the normal enimeies and spawn the boss after two seconds
          game.time.events.remove(greenEnemyLaunchTimer)
          game.time.events.remove(blueEnemyLaunchTimer)

          // Dramatic pause before boss
          game.time.events.add(1000, function () {
            bossLaunched = true
            launchBoss()
          })
        }
      }
    }

    function processPickup (player, pickup) {
      if (pickup.weaponType === 0) {
        for (var i = hearts.children.length - 1; i >= 0; i--) {
          if (!hearts.children[i].alive) {
            player.health += 20
            hearts.children[i].reset(hearts.children[i].x, hearts.children[i].y)
            break
          }
        }
        if (!pickupSound.isPlaying) { pickupSound.play() }
      } else if (pickup.weaponType === 1) {
        if (player.weaponLevel < 3) {
          player.weaponLevel++
        }
        if (!pickupSound.isPlaying) { pickupSound.play() }
      } else {
        player.potato = true
        if (!potatoSound.isPlaying) { potatoSound.play() }
      }
      pickup.kill()
    }

    function fireBullet () {
      var BULLET_SPEED = 400
      var BULLET_SPACING = 300
      switch (player.weaponLevel) {
        case 1:
          if (game.time.now > bulletTimer) {
            playLaserAudio()
            // Grab the first bullet
            let bullet = bullets.getFirstExists(false)
            if (bullet) {
              // Fire it from the tip of the ship with the correct angle
              let bulletOffset = 20 * Math.sin(game.math.degToRad(player.angle))
              bullet.reset(player.x + 10 + bulletOffset, player.y)
              bullet.angle = player.angle
              bullet.update = function () {
                if (player.potato) {
                  this.y = game.rnd.integerInRange(this.y - 15, this.y + 15)
                }
              }
              game.physics.arcade.velocityFromAngle(bullet.angle, BULLET_SPEED, bullet.body.velocity)
              bullet.body.velocity.y += player.body.velocity.y
              bulletTimer = game.time.now + BULLET_SPACING
            }
          }
          break
        case 2:
          if (game.time.now > bulletTimer) {
            playLaserAudio()
            for (let i = 0; i < 2; i++) {
              let bullet = bullets.getFirstExists(false)
              if (bullet) {
                // Fire it from the tip of the ship with the correct angle
                let bulletOffset = 20 * Math.sin(game.math.degToRad(player.angle))
                let bulletSpacing = 0
                if (i === 0) { bulletSpacing = -10 }
                if (i === 1) { bulletSpacing = 10 }
                bullet.reset(player.x + 10 + bulletOffset, player.y + bulletSpacing)

                // Add Spread
                let spreadAngle = 0
                bullet.angle = player.angle + spreadAngle
                game.physics.arcade.velocityFromAngle(spreadAngle, BULLET_SPEED, bullet.body.velocity)
                bullet.body.velocity.y += player.body.velocity.y
                bullet.update = function () {
                  if (player.potato) {
                    this.y = game.rnd.integerInRange(this.y - 15, this.y + 15)
                  }
                }
              }
            }
            bulletTimer = game.time.now + BULLET_SPACING
          }
          break
        case 3:
          if (game.time.now > bulletTimer) {
            playLaserAudio()
            for (let i = 0; i < 3; i++) {
              let bullet = bullets.getFirstExists(false)
              if (bullet) {
                // Fire it from the tip of the ship with the correct angle
                let bulletOffset = 20 * Math.sin(game.math.degToRad(player.angle))
                bullet.reset(player.x + 10 + bulletOffset, player.y)

                // Add Spread
                let spreadAngle = 0
                if (i === 0) { spreadAngle = -20 }
                if (i === 1) { spreadAngle = 0 }
                if (i === 2) { spreadAngle = 20 }
                bullet.angle = player.angle + spreadAngle
                game.physics.arcade.velocityFromAngle(spreadAngle, BULLET_SPEED, bullet.body.velocity)
                bullet.body.velocity.y += player.body.velocity.y
                bullet.update = function () {
                  if (player.potato) {
                    this.y = game.rnd.integerInRange(this.y - 15, this.y + 15)
                  }
                }
              }
            }
            bulletTimer = game.time.now + BULLET_SPACING
          }
          break
      }
      player.lastShot = game.time.now
    }

    function launchGreenEnemy () {
      var enemy = greenEnemies.getFirstExists(false)
      if (enemy) {
        enemy.reset(game.width + enemy.width * 2, game.rnd.integerInRange(0, game.height))
        enemy.body.velocity.x = -ENEMY_SPEED
        enemy.body.velocity.y = game.rnd.integerInRange(-300, 300)
        enemy.body.drag.y = 100

        enemy.trail.start(false, 800, 1)

        // Update function for each enemy ship
        enemy.update = function () {
          if (game.physics.arcade.distanceBetween(enemy, player) > 175 && enemy.x > 175) {
            var angle = game.physics.arcade.moveToObject(enemy, player, ENEMY_SPEED)
            enemy.angle = game.math.radToDeg(angle)
          }

          enemy.trail.x = enemy.x + 10
          enemy.trail.y = enemy.y

          if (enemy.x < 0) {
            enemy.kill()
            enemy.x = game.width + enemy.width
          }
        }
      }

      // Spawn another enemy timer
      greenEnemyLaunchTimer = game.time.events.add(game.rnd.integerInRange(greenEnemySpacing, greenEnemySpacing + 500), launchGreenEnemy)
    }

    function launchBlueEnemy () {
      var startingX = game.width - 200
      var startingY = Math.random() < 0.5 ? -1 : 1
      var verticalSpeed = 180
      var spread = 60
      var verticalSpacing
      bossLaunched ? verticalSpacing = 100 : verticalSpacing = 70
      var frequency = 70
      var numEnemiesInWave = 5

      // Launch wave
      for (var i = 0; i < numEnemiesInWave; i++) {
        var enemy = blueEnemies.getFirstExists(false)
        if (enemy) {
          var yPosition = startingY > 0 ? 0 + enemy.height : game.height - enemy.height
          enemy.reset(startingX, yPosition + (-verticalSpacing * i) * startingY)
          enemy.body.velocity.y = verticalSpeed * startingY

          // Set up firing
          var bulletSpeed = 400
          var firingDelay = 1000
          bossLaunched ? enemy.bullets = 3 : enemy.bullets = 1
          enemy.lastShot = 0

          // Update function for each enemy
          enemy.update = function () {
            // Wave movement
            this.body.x = startingX + Math.sin((this.y) / frequency) * spread

            // Squish and rotate hip for "banking"
            bank = Math.cos((this.y + 60) / frequency)
            this.scale.x = 0.25 - Math.abs(bank) / 8
            this.angle = 180 - bank * 2

            // Fire
            var enemyBullet = blueEnemyBullets.getFirstExists(false)
            if (enemyBullet &&
                this.alive &&
                this.bullets &&
                this.y > game.height / 8 &&
                this.y < game.height - game.height / 8 &&
                game.time.now > firingDelay + this.lastShot) {
              this.lastShot = game.time.now
              this.bullets--
              enemyBullet.reset(this.x, this.y + this.height / 2)
              enemyBullet.damageAmount = this.damageAmount
              var angle = game.physics.arcade.moveToObject(enemyBullet, player, bulletSpeed)
              enemyBullet.angle = game.math.radToDeg(angle)
            }

            // Clean up Out of Bounds enemies
            if (this.y > game.height + 500 || this.y < -500) {
              this.kill()
              this.y = -20
            }
          }
        }
      }

      // Send another wave
      if (!bossLaunched) { blueEnemyLaunchTimer = game.time.events.add(game.rnd.integerInRange(blueEnemySpacing, blueEnemySpacing + 4000), launchBlueEnemy) }
    }

    function launchBoss () {
      boss.reset(game.width + boss.width, game.height / 2)
      boss.health = 3500
      bossAttackTimer = game.time.now + bossAttackRate
    }

    function addEnemyEmitterTrail (enemy) {
      var enemyTrail = game.add.emitter(enemy.x, player.y - 10, 100)
      enemyTrail.width = 10
      enemyTrail.makeParticles('explosion', [1, 2, 3, 4, 5])
      enemyTrail.setXSpeed(20, -20)
      enemyTrail.setRotation(50, -50)
      enemyTrail.setAlpha(0.4, 0, 800)
      enemyTrail.setScale(0.01, 0.1, 0.01, 0.1, 1000, Phaser.Easing.Quintic.Out)
      enemy.trail = enemyTrail
    }

    function shipCollide (player, enemy) {
      enemy.kill()
      var heart = hearts.getFirstExists()
      if (heart) { heart.kill() }
      player.damage(enemy.damageAmount)

      if (player.alive) {
        if (!oww.isPlaying) { oww.play() }
        triggerExplosion(enemy)
        triggerExplosion(player)
      } else {
        if (!owwLong.isPlaying) { owwLong.play() }
        triggerPlayerExplosion(player)
      }
    }

    function triggerExplosion (target) {
      var explosion = explosions.getFirstExists(false)
      explosion.reset(target.body.x + target.body.halfWidth, target.body.y + target.body.halfHeight)
      explosion.body.velocity.y = target.body.velocity.y
      explosion.body.velocity.x = target.body.velocity.x
      explosion.alpha = 0.7
      explosion.play('explosion', 30, false, true)
    }

    function triggerBossExplosion (target) {
      var explosion = explosions.getFirstExists(false)
      explosion.reset(target.x + Math.floor(target.width / 2), target.y + Math.floor(target.height / 2))
      explosion.body.velocity.y = target.velocity.y
      explosion.body.velocity.x = target.velocity.x
      explosion.alpha = 0.7
      explosion.play('explosion', 30, false, true)
    }

    function triggerPlayerExplosion (player) {
      playerDeath.x = player.x
      playerDeath.y = player.y
      playerDeath.start(false, 1000, 10, 10)
    }

    function dropPickup (location) {
      var index = Math.floor(Math.random() * 10)
      var drop
      if (player.weaponLevel !== 3 && index < 6 || isWeaponDeficient()) {
        index = 1
        drop = pickup.children[1]
      } else if (!player.potato && index > 7) {
        index = 2
        drop = pickup.children[2]
      } else if (index > 6 || index < 2) {
        index = 0
        drop = pickup.children[0]
      }
      if (drop && !drop.alive) {
        drop.reset(location.body.x + drop.width, location.body.y + drop.height)
        drop.body.velocity.x = -300
        drop.visible = true
        drop.alive = true
        drop.weaponType = index
        drop.update = function () {
          if (drop.x < -drop.width) {
            drop.kill()
          }
        }
      }
    }

    function isWeaponDeficient () {
      return player.weaponLevel < 2 && score > 5000 || player.weaponLevel < 3 && score > 7500
    }

    function hitEnemy (enemy, bullet) {
      if (enemy.finishOff) {
        var boundsA = enemy.getBounds()
        var boundsB = bullet.getBounds()
        var rectangle = Phaser.Rectangle.intersection(boundsA, boundsB)
        rectangle.velocity = {x: enemy.body.velocity.x, y: enemy.body.velocity.y}
        triggerBossExplosion(rectangle)
      } else {
        triggerExplosion(enemy)
      }
      if (enemy.finishOff && enemy.health < 1000) {
        enemy.finishOff()
      } else {
        if (!bewm.isPlaying) { bewm.play() }
        enemy.damage(enemy.damageAmount)
      }
      bullet.kill()
      var variableRate = player.weaponType === 2 ? 60 : 17
      var dropChance = Math.floor(Math.random() * variableRate)

      if ((dropChance === 0 && !bossLaunched || isWeaponDeficient()) && dropTimer < game.time.now) {
        dropPickup(enemy)
        dropTimer = game.time.now + dropRate
      }

      // Increment score
      score += enemy.damageAmount * 10

      updateGameState()
    }

    function enemyShootsPlayer (player, bullet) {
      if (bullet.key !== 'deathRay') {
        bullet.kill()
      }

      var heart = hearts.getFirstExists(true)
      if (heart) { heart.kill() }

      player.damage(bullet.damageAmount)
      if (player.alive) {
        if (!oww.isPlaying) { oww.play() }
        triggerExplosion(player)
      } else {
        if (!owwLong.isPlaying) { owwLong.play() }
        triggerPlayerExplosion(player)
      }
    }

    function playLaserAudio () {
      var clipNum = Math.floor(Math.random() * 3)
      lasers[clipNum].play()
    }

    function restart () {
      // Reset the enemies
      greenEnemies.callAll('kill')
      game.time.events.remove(greenEnemyLaunchTimer)

      blueEnemies.callAll('kill')
      blueEnemyBullets.callAll('kill')
      game.time.events.remove(blueEnemyLaunchTimer)
      boss.kill()
      deathRay.kill()
      deathRayCollider.kill()
      game.time.events.remove(bossLaunchTimer)

      // Revive the player
      player.weaponLevel = 1
      player.revive()
      player.health = 100
      player.potato = false
      hearts.forEach(function (heart) {
        heart.reset(heart.x, heart.y)
      })

      // Remove one heart
      var _heart = hearts.getFirstExists()
      if (_heart) { _heart.kill() }

      score = 0

      pickup.forEach(function (drop) {
        drop.kill()
      })

      // Hide the screens
      gameOver.visible = false
      lowcoRip.visible = false
      gameWin.visible = false
      gotemLeft.visible = false
      gotemRight.visible = false

      // Reset pacing
      greenEnemySpacing = 1000
      blueEnemyLaunched = false
      bossLaunched = false

      startGame()
    }
  }
}



